.search {
  width: 100%;
  position: relative;
  
}

.searchTerm {
  float: left;
  text-align: left;
  width: 100%;
  border: 3px solid #182628;
  padding: 5px;
  height: 45px;
  border-radius: 5px;
  outline: none;
  color: #9DBFAF;
}

.searchTerm:focus{
  color: #05386B;
}

.searchButton {
  position: absolute;  
  right: -50px;
  width: 45px;
  height: 45px;
  border: 1px solid #182628;
  background: #182628;
  text-align: center;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  font-size: 20px;
}

/*Resize the wrap to change*/
.wrap{
  width: 35%;
  position: absolute;
  top: 5.3%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
